import React, { useEffect, useMemo, useState, useRef } from "react";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import DragAndDropComponent from "../../components/DragAndDrop/DragAndDrop";
import CardWithDragAndDrop from "../../components/Card/cardwithdraganddrop";
import { Image } from "react-bootstrap";
import useDroppableArea from "../../hooks/useDroppableArea";
import Loader from "../../components/Loader/Loader";
import ReusableTable from "../../components/Table/Table";
import {
  setDroppedObjectData,
  setIsDropped,
} from "../../store/droppedObjectSlice";
import { getCardData, getTableData, tableColumns } from "./dataHelpers";
import RevisionFloatToolbarNativeCta from "./revisionFloatToolbarNativeCta";

const RevisionFloat = () => {
  const { initializeDroppableArea, loading } = useDroppableArea();
  const dispatch = useDispatch();

  // ‚úÖ Use shallowEqual to prevent unnecessary re-renders
  const { droppedObjectData, isDropped, loadingParentDetails } = useSelector(
    (state) => ({
      droppedObjectData: state.droppedObject.droppedObjectData,
      isDropped: state.droppedObject.isDropped,
      loadingParentDetails: state.droppedObject.loadingParentDetails,
    }),
    shallowEqual // ‚úÖ Prevents re-renders if values are unchanged
  );

  const [tableKey, setTableKey] = useState(0);
  const [tableData, setTableData] = useState([]);
  const [isTableLoading, setIsTableLoading] = useState(false);

  console.log("-----revision float logic----- starts");

  // ‚úÖ Ensure droppable area is only initialized **once**
  useEffect(() => {
    if (!isDropped) {
      initializeDroppableArea();
    }
  }, [isDropped, initializeDroppableArea]);

  // ‚úÖ Optimize `isTableLoading` update (only update if necessary)
  useEffect(() => {
    if (isTableLoading !== loadingParentDetails) {
      setIsTableLoading(loadingParentDetails);
    }
  }, [loadingParentDetails, isTableLoading]);

  // ‚úÖ Memoize table data to avoid unnecessary calculations
  const newTableData = useMemo(() => getTableData(droppedObjectData), [droppedObjectData]);

  // ‚úÖ Prevent unnecessary re-renders by checking for data changes
  useEffect(() => {
    if (JSON.stringify(newTableData) !== JSON.stringify(tableData)) {
      setTableData(newTableData);
      setTableKey((prevKey) => prevKey + 1);
    }
  }, [newTableData]);

  // ‚úÖ Memoize card data
  const cardData = useMemo(() => getCardData(droppedObjectData), [droppedObjectData]);

  // ‚úÖ Reset state only when needed
  const handleHomeClick = () => {
    initializeDroppableArea();
    dispatch(setIsDropped(false));
    dispatch(
      setDroppedObjectData({
        draggedData: {},
        parentDetails: [],
        versions: [],
        initialDraggedData: [],
      })
    );
    setTableData([]);
  };

  // ‚úÖ Memoize filtered columns
  const filteredColumns = useMemo(() => {
    if (droppedObjectData?.draggedData?.Type === "Document") {
      return tableColumns;
    } else {
      return tableColumns.filter((column) => column.accessorKey !== "relationship");
    }
  }, [droppedObjectData]);

  console.log("----revision float logic---- ends");

  return (
    <>
      {!isDropped && !loading && !isTableLoading && <DragAndDropComponent />}
      {loading && <Loader />}
      {isDropped && (
        <>
          <div className="content-wrapper py-3 border-bottom">
            <div className="d-flex">
              <div className="p-0 pt-4">
                <Image
                  src="https://thewhitechamaleon.github.io/testrapp/images/home.png"
                  alt="home-icon"
                  className="home-icon"
                  onClick={handleHomeClick}
                />
              </div>
              {cardData && <CardWithDragAndDrop data={cardData} />}
            </div>
          </div>

          {isTableLoading ? (
            <div className="loading-indicator mt-5">
              <Loader />
            </div>
          ) : tableData.length > 0 ? (
            <div className="wrapper-cta">
              <RevisionFloatToolbarNativeCta />
              <ReusableTable
                key={tableKey}
                data={tableData}
                columns={filteredColumns}
                widgetType="Revision_FLoat_Widget"
              />
            </div>
          ) : (
            <div className="no-data-banner">No Parent object(s) found</div>
          )}
        </>
      )}
    </>
  );
};

export default RevisionFloat;
